*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: white;
  height: 100%;
}
body {
  display: flex;
  justify-content: center; 
  align-items: center; 
  flex-direction: column;
  height: 100vh; 
  margin: 0 auto;
  background-image: linear-gradient(to bottom, #013a9d, #6d9df6, #f5b111);
  background-repeat: no-repeat;
  background-attachment: fixed;
}
form {
  padding-top: 1rem;
  padding-bottom: 1rem;
}
input {
  font-size: 1.15rem;
  width:20ch;
  padding: .5rem;
  border-radius: 5px;
  
}
button {
  font-size: 1.1rem;
  padding: .5rem;
  border-radius: 5px;
  border: none;
  margin-left: .5rem;

}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; 
  align-items: center; 
  height: 100vh;
  max-width: 450px;
  padding: 1rem; 

}
.title-block {
  display: flex;
  flex-direction: column;
  height: 4rem;
  padding: 1rem;
  font-weight: 700;
}
#resolved-location {
  font-size: .75rem;
  font-style: italic;
}

#currently{
  margin-top: 2rem;
  /* max-width: 450px; */
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  background: #6d9df6;
  border-radius: 20px;
  padding-bottom: 2rem;
}

#date {
  font-size: .75rem;
  font-weight: 100;
}
#current-temp {
  padding-top: 1rem;
  font-size: 3rem;
}

#wx-icon img {
  padding-top: 1rem;
  object-fit: contain;
}

.wx-message {
  padding: 2rem;
}
.error-message {
  padding: 2rem;
  display: none;
}
.error-display {
  background: red;
  display: block;
}`, "",{"version":3,"sources":["webpack://./src/styles/styles.css"],"names":[],"mappings":"AAAA,eAAe;AACf;;;EAGE,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,4DAA4D;EAC5D,YAAY;EACZ,YAAY;AACd;AACA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,sBAAsB;EACtB,aAAa;EACb,cAAc;EACd,uEAAuE;EACvE,4BAA4B;EAC5B,4BAA4B;AAC9B;AACA;EACE,iBAAiB;EACjB,oBAAoB;AACtB;AACA;EACE,kBAAkB;EAClB,UAAU;EACV,cAAc;EACd,kBAAkB;;AAEpB;AACA;EACE,iBAAiB;EACjB,cAAc;EACd,kBAAkB;EAClB,YAAY;EACZ,kBAAkB;;AAEpB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,2BAA2B;EAC3B,mBAAmB;EACnB,aAAa;EACb,gBAAgB;EAChB,aAAa;;AAEf;AACA;EACE,aAAa;EACb,sBAAsB;EACtB,YAAY;EACZ,aAAa;EACb,gBAAgB;AAClB;AACA;EACE,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,sBAAsB;EACtB,UAAU;EACV,aAAa;EACb,8BAA8B;EAC9B,qBAAqB;EACrB,mBAAmB;EACnB,mBAAmB;EACnB,mBAAmB;EACnB,oBAAoB;AACtB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;AAClB;AACA;EACE,iBAAiB;EACjB,eAAe;AACjB;;AAEA;EACE,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA;EACE,aAAa;AACf;AACA;EACE,aAAa;EACb,aAAa;AACf;AACA;EACE,eAAe;EACf,cAAc;AAChB","sourcesContent":["/* styles.css */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n}\n\nhtml {\n  font-family: \"Segoe UI\", Tahoma, Geneva, Verdana, sans-serif;\n  color: white;\n  height: 100%;\n}\nbody {\n  display: flex;\n  justify-content: center; \n  align-items: center; \n  flex-direction: column;\n  height: 100vh; \n  margin: 0 auto;\n  background-image: linear-gradient(to bottom, #013a9d, #6d9df6, #f5b111);\n  background-repeat: no-repeat;\n  background-attachment: fixed;\n}\nform {\n  padding-top: 1rem;\n  padding-bottom: 1rem;\n}\ninput {\n  font-size: 1.15rem;\n  width:20ch;\n  padding: .5rem;\n  border-radius: 5px;\n  \n}\nbutton {\n  font-size: 1.1rem;\n  padding: .5rem;\n  border-radius: 5px;\n  border: none;\n  margin-left: .5rem;\n\n}\n\n.app-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start; \n  align-items: center; \n  height: 100vh;\n  max-width: 450px;\n  padding: 1rem; \n\n}\n.title-block {\n  di